import { Component, OnInit, Input} from '@angular/core';
import { typeList, TypeObject } from '../../../../../commonobject/commonobject';

@Component({
    selector: 'app-dictionarytypemodal',
    templateUrl: './dictionarymodal.component.html'
  })

  export class DictionaryTypeModalComponent {
    typeList: TypeObject[] = [
        { text: 'Basic', value: true },
        { text: 'Complex', value: false }
    ];
    selectTkeyResult:boolean;
    selectTValueResult:boolean;
    selectedTkeyType:string;
    selectedTValueType:string;
    basicList:TypeObject[]=typeList;
    tKeyModelId:string;
    tKeyModelCode:string;
    tValueModelId:string;
    tValueModelCode:string
  }


