import { ComponentFactoryResolver, Injector } from '@angular/core';
import { CommonComponentDesignerModule } from './acmpdesigner/ccmp.module';
import { CommonComponentDesignerComponent } from './acmpdesigner/ccmp.component';
import { PackageView } from '@farris/ide-devkit';
export class ActionComponentDesignerPackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(CommonComponentDesignerComponent);
    const moduleRef = this.injector.get(CommonComponentDesignerModule) as CommonComponentDesignerModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}



