import { Component, Input, EventEmitter } from '@angular/core';
import { TypeObject } from '../../../commonobject/commonobject';
import { BasicTypeModalComponent } from './datatypemodal/basic/basictypemodal.component';
import { BasicType, ComplexType, ListType, UnSupportType, DictionaryType } from '@gsp-cmp/component';
import { ComplexTypeModalComponent } from './datatypemodal/complex/complexmodal.component';
import { ListTypeModalComponent } from './datatypemodal/list/listmodal.component';
import { DictionaryTypeModalComponent } from './datatypemodal/dictionary/dictionarymodal.component';
import { isNullOrUndefined } from 'util';
import { DialogService, DialogCloseResult, DialogAction } from '@progress/kendo-angular-dialog'
import { ComponentParameter } from '@gsp-cmp/component';

@Component({
  selector: 'app-newparam',
  templateUrl: './newparammodal.component.html'
})

export class NewParamModalComponent{
  dataTypeList: Array<TypeObject> = [
    { text: 'Basic', value: 0 },
    { text: 'List<T>', value: 1 },
    { text: 'Dictionary<TKey,TValue>', value: 2 },
    { text: 'Complex', value: 3 },
    { text: 'UnSupport', value: 4 }
  ]
  status: Array<boolean> = [true, false];
  dataTypeDescription:string;
  @Input() newParam: ComponentParameter;

  constructor(private dialogService: DialogService) { }

  setParamType(){
    if(this.newParam.Classification==undefined){
      alert("");
      return;
    }
    if (this.newParam.Classification == 0) {
      this.setBasicTypeInfo();
    }
    else if (this.newParam.Classification == 1) {
      this.setListTypeInfo();
    }
    else if (this.newParam.Classification == 2) {
      this.setDictionaryTypeInfo();
    }
    else if (this.newParam.Classification == 3) {
      this.setComplexTypeInfo();
    }
    else if (this.newParam.Classification == 4) {
     this.setUnSupportTypeInfo();
     this.dataTypeDescription="Object";
    }
  }

  classificationChange(value:any){
    this.dataTypeDescription=null;
  }

  setBasicTypeInfo() {
    let title = '';
    let dialogRef = this.openModal(BasicTypeModalComponent, title,400);
    const actionEmitter = dialogRef.dialog.instance.action;
    dialogRef.dialog.instance.action = new EventEmitter<any>();
    const sub = dialogRef.dialog.instance.action.subscribe(action => {
      if (action.text == "") {
        actionEmitter.emit(action);
      }
      else {
        actionEmitter.emit(action);
      }
    });
    //
    const result = dialogRef.result.subscribe((dialogResult) => {
      sub.unsubscribe();
      if (!(dialogResult instanceof DialogCloseResult)) {
        let actionName = (dialogResult as DialogAction).text;
        if (actionName == "") {
          let result = dialogRef.content.instance;
          let dataType = new BasicType();
          dataType.ParamType = result.selectedType;
          this.newParam.DataType = dataType;
          this.dataTypeDescription=this.newParam.DataType.getDescription();
        }
      }
    });
  }

  setComplexTypeInfo() {
    let title = '';
    let dialogRef = this.openModal(ComplexTypeModalComponent, title,400);
    const actionEmitter = dialogRef.dialog.instance.action;
    dialogRef.dialog.instance.action = new EventEmitter<any>();
    const sub = dialogRef.dialog.instance.action.subscribe(action => {
      if (action.text == "") {
        actionEmitter.emit(action);
      }
      else {
        actionEmitter.emit(action);
      }
    });
    //
    const result = dialogRef.result.subscribe((dialogResult) => {
      sub.unsubscribe();
      if (!(dialogResult instanceof DialogCloseResult)) {
        let actionName = (dialogResult as DialogAction).text;
        if (actionName == "") {
          let result = dialogRef.content.instance;
          let dataType = new ComplexType();
          dataType.ModelId = result.modelId;
          dataType.ModelName = result.modelName;
          this.newParam.DataType = dataType;
          this.dataTypeDescription=this.newParam.DataType.getDescription();
        }
      }
    });
  }

  setListTypeInfo() {
    let title = 'List';
    let dialogRef = this.openModal(ListTypeModalComponent, title,400);
    const actionEmitter = dialogRef.dialog.instance.action;
    dialogRef.dialog.instance.action = new EventEmitter<any>();
    const sub = dialogRef.dialog.instance.action.subscribe(action => {
      if (action.text == "") {
        actionEmitter.emit(action);
      }
      else {
        actionEmitter.emit(action);
      }
    });
    //
    const result = dialogRef.result.subscribe((dialogResult) => {
      sub.unsubscribe();
      if (!(dialogResult instanceof DialogCloseResult)) {
        let actionName = (dialogResult as DialogAction).text;
        if (actionName == "") {
          let result = dialogRef.content.instance;
          if (result.selectTypeResult == true) {
            let dataType = new ListType();
            dataType.IsBasic = true;
            dataType.ModelId = result.selectedType;
            dataType.ModelName = result.selectedType;
            this.newParam.DataType = dataType;
            this.dataTypeDescription=this.newParam.DataType.getDescription();
          }
          else if (result.selectTypeResult == false) {
            let dataType = new ListType();
            dataType.IsBasic = false;
            dataType.ModelId = result.modelId;
            dataType.ModelName = result.modelName;
            this.newParam.DataType = dataType;
            this.dataTypeDescription=this.newParam.DataType.getDescription();
          }
        }
      }
    });
  }

  setDictionaryTypeInfo() {
    let title = 'Dictionary';
    let dialogRef = this.openModal(DictionaryTypeModalComponent, title,450);
    const actionEmitter = dialogRef.dialog.instance.action;
    dialogRef.dialog.instance.action = new EventEmitter<any>();
    const sub = dialogRef.dialog.instance.action.subscribe(action => {
      if (action.text == "") {
        
        actionEmitter.emit(action);
      }
      else {
        actionEmitter.emit(action);
      }
    });
    //
    const result = dialogRef.result.subscribe((dialogResult) => {
      sub.unsubscribe();
      if (!(dialogResult instanceof DialogCloseResult)) {
        let actionName = (dialogResult as DialogAction).text;
        if (actionName == "") {
          let result = dialogRef.content.instance;
          let dataType = new DictionaryType();
          if (result.selectTkeyResult == true) {
            dataType.IsTkeyBasic = true;
            dataType.Tkey = result.selectedTkeyType;
            dataType.TkeyModelId = result.selectedTkeyType;
          }
          else if (result.selectTkeyResult == false) {
            dataType.IsTkeyBasic = false;
            dataType.Tkey = result.tKeyModelCode;
            dataType.TkeyModelId = result.tKeyModelId;
          }
          if (result.selectTValueResult == true) {
            dataType.IsTValueBasic = true;
            dataType.TValue = result.selectedTValueType;
            dataType.TValueModelId = result.selectedTValueType;
          }
          else if (result.selectTValueResult == false) {
            dataType.IsTValueBasic = false;
            dataType.TValue = result.tValueModelCode;
            dataType.TValueModelId = result.tValueModelId;
          }
          if (!isNullOrUndefined(dataType.IsTkeyBasic) && !isNullOrUndefined(dataType.IsTValueBasic)) {
            this.newParam.DataType = dataType;
            this.dataTypeDescription=this.newParam.DataType.getDescription();
          }
        }
      }
    });
  }

  setUnSupportTypeInfo(){
    let dataType = new UnSupportType();
    dataType.ParamType = "Object";
    this.newParam.DataType = dataType;
  }

  openModal(component: any, title: any,width:any) {
    const dialogRef = this.dialogService.open({
      title: title,
      content: component,
      actions: [
        { text: '' },
        { text: '', primary: true }
      ],
      width: width
    });
    return dialogRef;
  }
}


