import { Component, OnInit, Input} from '@angular/core';
import { TypeObject, typeList } from '../../../../../commonobject/commonobject';

@Component({
    selector: 'app-listmodal',
    templateUrl: './listmodal.component.html'
  })

  export class ListTypeModalComponent {
    typeList: TypeObject[] = [
        { text: 'Basic', value: true },
        { text: 'Complex', value: false }
    ];
    basicList:TypeObject[]=typeList;
    selectTypeResult:boolean;
    selectedType:string;
    modelId:string;
    modelName:string;
  }


