import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { CommonModule } from '@angular/common';
import { ToolBarModule } from '@progress/kendo-angular-toolbar';
import { ButtonModule, ButtonsModule, ButtonGroupModule } from '@progress/kendo-angular-buttons';
import { LayoutModule, SplitterModule } from '@progress/kendo-angular-layout';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
import { GridModule } from '@progress/kendo-angular-grid';
import { DialogModule } from '@progress/kendo-angular-dialog';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { DropDownsModule, DropDownListModule } from '@progress/kendo-angular-dropdowns';
import { PopupModule } from '@progress/kendo-angular-popup';
import { ReactiveFormsModule } from '@angular/forms';
import { FarrisDialogModule } from '@farris/ui';
import { ComponentService } from './commonobject/service';
import { LabelModule } from '@progress/kendo-angular-label';
import { GSPMetadataServiceModule } from "@gsp-lcm/metadata-selector";
import { environment } from '../../../../environments/environment'
import { CommonComponentDesignerComponent } from './ccmp.component';
import { CmpOperationsComponent } from './ccmpoperations/ccmpoperations.component';
import { CmpBaseInfoComponent } from './ccmpbaseinfo/ccmpbaseinfo.component';
import { NewModelModalComponent } from './ccmpbaseinfo/modal/modelmodal.component';
import { EditParamModalComponent } from './ccmpoperations/modal/parammodal/editparammodal.component';
import { NewParamModalComponent } from './ccmpoperations/modal/parammodal/newparammodal.component';
import { NewMethodModalComponent } from './ccmpoperations/modal/methodmodal/newmethodmodal.component';
import { BasicTypeModalComponent } from './ccmpoperations/modal/parammodal/datatypemodal/basic/basictypemodal.component';
import { ListTypeModalComponent } from './ccmpoperations/modal/parammodal/datatypemodal/list/listmodal.component';
import { ComplexTypeModalComponent } from './ccmpoperations/modal/parammodal/datatypemodal/complex/complexmodal.component';
import { DictionaryTypeModalComponent } from './ccmpoperations/modal/parammodal/datatypemodal/dictionary/dictionarymodal.component';
// import {CommonComponentSelectorModule} from '@gsp-cmp/component';

@NgModule({
  declarations: [
    CommonComponentDesignerComponent,
    CmpOperationsComponent,
    CmpBaseInfoComponent,
    NewModelModalComponent,
    EditParamModalComponent,
    NewParamModalComponent,
    NewMethodModalComponent,
    BasicTypeModalComponent,
    ListTypeModalComponent,
    ComplexTypeModalComponent,
    DictionaryTypeModalComponent
  ],
  imports: [
    FormsModule,
    HttpModule,
    CommonModule,
    ToolBarModule,
    ButtonsModule,
    ButtonGroupModule,
    DialogModule,
    ReactiveFormsModule,
    InputsModule,
    DropDownListModule,
    PopupModule,
    TreeViewModule,
    LayoutModule,
    LabelModule,
    GridModule,
    LayoutModule,
    FarrisDialogModule,
    // CommonComponentSelectorModule,
    GSPMetadataServiceModule.forRoot(environment.SERVER_IP)
  ],
  providers: [ComponentService],
  entryComponents:[NewModelModalComponent,EditParamModalComponent,NewParamModalComponent
    ,NewMethodModalComponent,BasicTypeModalComponent,ListTypeModalComponent
    ,ComplexTypeModalComponent,DictionaryTypeModalComponent],
  bootstrap: [CommonComponentDesignerComponent]
})

export class CommonComponentDesignerModule { }



