import { Component, Input, OnInit, OnChanges, ViewChild, EventEmitter } from '@angular/core'
import { ComponentService } from '../commonobject/service';
import { isNullOrUndefined } from 'util';
import { TypeObject, typeList } from '../commonobject/commonobject';
import { NewMethodModalComponent } from './modal/methodmodal/newmethodmodal.component';
import { NewParamModalComponent } from './modal/parammodal/newparammodal.component';
import { EditParamModalComponent } from './modal/parammodal/editparammodal.component';
import { SelectAllCheckboxState, RowArgs } from '@progress/kendo-angular-grid'
import { DialogService, DialogCloseResult, DialogAction } from '@progress/kendo-angular-dialog'
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils'
import { ComponentMethod, CommonComponent, ComponentParameter } from '@gsp-cmp/component';

@Component({
    selector: 'app-ccmpoperations',
    templateUrl: './ccmpoperations.component.html'
})

export class CmpOperationsComponent implements OnInit, OnChanges {
    @Input() cmpmetadata: CommonComponent;
    currentParamList: ComponentParameter[] = [];
    currentOperation: ComponentMethod;
    selectedOpList: ComponentMethod[];
    selectedParamList: ComponentParameter[];
    selectedOpIds: string[] = [];
    selectedParamIds: string[] = [];
    selectAllOpState: SelectAllCheckboxState = 'unchecked';
    selectAllParamState: SelectAllCheckboxState = 'unchecked';
    newOperaion: ComponentMethod;
    newParam: ComponentParameter;
    currentParam: ComponentParameter;
    status: Array<boolean> = [true, false];
    isRowParamSelected = (e: RowArgs) => this.selectedParamIds.indexOf(e.dataItem.Id) >= 0;
    paramModelList: TypeObject[] = [];

    constructor(private service: ComponentService, private dialogService: DialogService) { }

    ngOnInit(): void {
        ;
        this.newOperaion = new ComponentMethod();
        this.newParam = new ComponentParameter();
        this.currentParam = new ComponentParameter();
        this.currentOperation = new ComponentMethod();
        if (!this.cmpmetadata.Methods||this.cmpmetadata.Methods.length==0) {
            this.cmpmetadata.Methods = [] as ComponentMethod[];
            this.cmpmetadata.Methods.push(this.currentOperation);
        }
        if (this.cmpmetadata.Methods.length > 0) {
            this.currentOperation = this.cmpmetadata.Methods[0];
            if (this.cmpmetadata.Methods[0].Parameters && this.cmpmetadata.Methods[0].Parameters.length > 0) {
                this.currentParamList = this.cmpmetadata.Methods[0].Parameters;
            }
        }
    }

    ngOnChanges() {
        ;
        this.currentOperation = new ComponentMethod();
        if (!this.cmpmetadata.Methods||this.cmpmetadata.Methods.length==0) {
            this.cmpmetadata.Methods = [] as ComponentMethod[];
            this.cmpmetadata.Methods.push(this.currentOperation);
        }
        if (this.cmpmetadata.Methods.length > 0) {
            this.currentOperation = this.cmpmetadata.Methods[0];
            this.selectedOpIds = [];
            this.selectedOpIds.push(this.currentOperation.Id);
            this.selectedOpList = [];
            if (this.cmpmetadata.Methods.length == 1) {
                this.selectAllOpState = 'checked';
            }
            else if (this.cmpmetadata.Methods.length > 1) {
                this.selectAllOpState = 'indeterminate';
            }
            this.selectedOpList.push(this.cmpmetadata.Methods[0]);
            if (this.cmpmetadata.Methods[0].Parameters && this.cmpmetadata.Methods[0].Parameters.length > 0) {
                this.currentParamList = this.cmpmetadata.Methods[0].Parameters;
            }
            this.selectedParamIds = [];
            //;
            this.selectedParamList = [];
            this.selectAllParamState = 'unchecked';
        }
    }

    getCurrentOperation({ dataItem }) {
        this.currentOperation = dataItem;
        this.currentParamList = this.currentOperation.Parameters;
    }

    /** */
    addOperation() {
        this.newOperaion = new ComponentMethod();
        if (!this.cmpmetadata.Methods) {
            this.cmpmetadata.Methods = [] as ComponentMethod[];
        }
        //
        if (!this.assemblyInfoEmptyCheck(this.cmpmetadata)) {
            const dialogRef = this.dialogService.open({
                title: '',
                content: NewMethodModalComponent,
                actions: [
                    { text: '' },
                    { text: '', primary: true }
                ],
                width: 360,
                height: 280
            });
            //
            const intance = dialogRef.content.instance;
            intance.newOperaion = this.newOperaion;
            const actionEmitter = dialogRef.dialog.instance.action;
            dialogRef.dialog.instance.action = new EventEmitter<any>();
            const sub = dialogRef.dialog.instance.action.subscribe(action => {
                if (action.text == "") {
                    if (!this.newOperaionEmptyCheck(this.newOperaion)) {
                        actionEmitter.emit(action);
                    }
                }
                else {
                    actionEmitter.emit(action);
                }
            });
            //
            dialogRef.result.subscribe((dialogResult) => {
                sub.unsubscribe();
                if (!(dialogResult instanceof DialogCloseResult)) {
                    let actionName = (dialogResult as DialogAction).text;
                    //;
                    if (actionName == "") {
                        this.newOperaion = Object.assign(new ComponentMethod(), dialogRef.content.instance.newOperaion);
                        this.newOperaion.Id = this.service.getGuid();
                        this.addNewOperaion(this.newOperaion);
                    }
                }
            });
        }
    }

    assemblyInfoEmptyCheck(metadata: CommonComponent): boolean {
        if (isNullOrEmptyString(metadata.AssemblyName) || isNullOrUndefined(metadata.AssemblyName)) {
            alert("");
            return false;
        }
        if (isNullOrEmptyString(metadata.FullClassName) || isNullOrUndefined(metadata.FullClassName)) {
            alert("");
            return false;
        }
        if (!this.currentOperation.Code) {
            alert("");
            return false;
        }
        return true;
    }

    newOperaionEmptyCheck(newOperaion: ComponentMethod): boolean {
        if (isNullOrEmptyString(newOperaion.Code) || isNullOrUndefined(newOperaion.Code)) {
            alert("");
            return true;
        }
        if (isNullOrEmptyString(newOperaion.Name) || isNullOrUndefined(newOperaion.Name)) {
            alert("");
            return true;
        }
        return false;
    }

    addNewOperaion(newOperaion: ComponentMethod) {
        newOperaion.Id = this.service.getGuid();
        this.cmpmetadata.Methods.push(Object.assign(new ComponentMethod(), newOperaion));
    }

    addParam() {
        if(this.assemblyInfoEmptyCheck(this.cmpmetadata)){
            const dialogRef = this.dialogService.open({
                title: '',
                content: NewParamModalComponent,
                actions: [
                    { text: '' },
                    { text: '', primary: true }
                ],
                width: 400,
                height: 460
            });
            // this.getParamModelList();
            this.newParam = new ComponentParameter();
            //
            const intance = dialogRef.content.instance;
            intance.newParam = this.newParam;
            const actionEmitter = dialogRef.dialog.instance.action;
            dialogRef.dialog.instance.action = new EventEmitter<any>();
            var that = this;
            const sub = dialogRef.dialog.instance.action.subscribe(action => {
                if (action.text == "") {
                    //;
                    let param: ComponentParameter = that.newParam;
                    if (this.CheckBeforeAdd(param)) {
                        actionEmitter.emit(action);
                    }
                }
                else {
                    actionEmitter.emit(action);
                }
            });
            //
            dialogRef.result.subscribe((dialogResult) => {
                sub.unsubscribe();
                if (!(dialogResult instanceof DialogCloseResult)) {
                    let actionName = (dialogResult as DialogAction).text;
                    if (actionName == "") {
                        //;
    
    
                        this.newParam = Object.assign(new ComponentParameter(), dialogRef.content.instance.newParam);
                        this.newParam.Id = this.service.getGuid();
                        this.currentParamList.push(this.newParam);
                        this.currentOperation.Parameters = this.currentParamList;
                        if (this.currentParamList.length == 1) {
                            this.selectAllParamState = 'checked';
                        }
                        else if (this.currentParamList.length > 1) {
                            this.selectAllParamState = 'indeterminate';
                        }
                    }
                }
            });
        }
       
    }
    CheckBeforeAdd(param: ComponentParameter) {
        if (!param.Code) {
            alert("")
            return false;
        }
        if (!param.Name) {
            alert("")
            return false;
        }
        if (!param.DataType) {
            alert("")
            return false;
        }
        return true;

    }
    newParamConfirm() {
        // if (isNullOrEmptyString(this.newParam.Code) || isNullOrUndefined(this.newParam.Code)) {
        //     alert("");
        //     return;
        // }
        // if (isNullOrEmptyString(this.newParam.Name) || isNullOrUndefined(this.newParam.Name)) {
        //     alert("");
        //     return;
        // }
        // if (isNullOrEmptyString(this.newParam.Code) || isNullOrUndefined(this.newParam.Code)) {
        //     alert("");
        //     return;
        // }
        // if (isNullOrEmptyString(this.newParam.Code) || isNullOrUndefined(this.newParam.Code)) {
        //     alert("");
        //     return;
        // }
        // if (!this.currentParamList) {
        //     this.currentParamList = [];
        // }
        // if (this.currentOperation.Parameters && this.currentOperation.Parameters.length > 0) {
        //     let tempIndex = this.currentOperation.Parameters.findIndex(value => value.Code == this.newParam.Code && value.Id != this.newParam.Id);
        //     if (tempIndex != (-1)) {
        //         alert("" + this.currentOperation.Code + "" + this.newParam.Code + "");
        //         return;
        //     }
        // }
        // this.newParam.Id = this.service.getGuid();
        // if (!this.currentOperation.Parameters) {
        //     this.currentOperation.Parameters = [] as ComponentParameter[];
        // }
        // this.currentOperation.Parameters.push(this.newParam);
        // let tempIndex = this.cmpmetadata.Methods.findIndex(value => value.Id == this.currentOperation.Id);
        // this.cmpmetadata.Methods.splice(tempIndex, 1, this.currentOperation);
        // this.currentParamList = this.currentOperation.Parameters;
    }

    deleteOpList() {
        //;
        if (!this.selectedOpList || this.selectedOpList.length <= 0) {
            alert("");
            return;
        }
        if (this.selectedOpList && this.selectedOpList.length > 0) {
            this.selectedOpList.forEach(element => {
                this.service.deleteSpecifiedElement(this.cmpmetadata.Methods, element);
                this.service.deleteSpecifiedElement(this.selectedOpIds, element.Id);
            });
        }
        if (this.cmpmetadata.Methods && this.cmpmetadata.Methods.length > 0) {
            this.currentOperation = this.cmpmetadata.Methods[0];
            if (this.cmpmetadata.Methods[0].Parameters) {
                this.currentParamList = this.cmpmetadata.Methods[0].Parameters;
            }
            else {
                this.currentParamList = [] as ComponentParameter[];
            }
        }
        else {
            this.currentOperation = new ComponentMethod();
            this.currentParamList = [] as ComponentParameter[];
        }
        this.selectAllOpState = 'unchecked';
        this.selectAllParamState = 'unchecked';
    }

    deleteParamList() {
        //;
        if (!this.selectedParamList || this.selectedParamList.length <= 0) {
            alert("");
            return;
        }
        this.selectedParamList.forEach(element => {
            this.service.deleteSpecifiedElementById(this.currentParamList, element);
            this.service.deleteSpecifiedElement(this.selectedParamIds, element.Id);
        });
        this.currentOperation.Parameters = this.currentParamList;
        let tempIndex = this.cmpmetadata.Methods.findIndex(value => value.Id == this.currentOperation.Id);
        this.cmpmetadata.Methods.splice(tempIndex, 1, this.currentOperation);
        this.selectAllParamState = 'unchecked';
    }

    cellClickHandler({ dataItem }) {
        this.currentParam = Object.assign(new ComponentParameter(), dataItem);
        this.resetParamRowSelection(this.currentParam);
    }

    editParamConfirm() {
        let isExistIndex = this.currentParamList.findIndex(value => value.Code == this.currentParam.Code && value.Id != this.currentParam.Id);
        if (isExistIndex != (-1)) {
            alert("" + this.currentParam.Code + "");
            return;
        }
        let tempIndex = this.currentParamList.findIndex(value => value.Id == this.currentParam.Id);
        if (tempIndex != (-1)) {
            this.currentParamList.splice(tempIndex, 1, this.currentParam);
        }
        // this.editParamDialog.close();
    }

    /** */
    getParamClassification(param: ComponentParameter): string {
        if (param.Classification == 0) {
            return 'Basic';
        }
        else if (param.Classification == 1) {
            return 'List<T>';
        }
        else if (param.Classification == 2) {
            return 'Dictionary<TKey,TValue>';
        }
        else if (param.Classification == 3) {
            return 'Complex';
        }
        else if (param.Classification == 4) {
            return 'UnSupport';
        }
        else {
            return "";
        }
    }

    /** */
    getParamTypeDescription(param: ComponentParameter) {
        if (param && param.DataType) {
            return param.DataType.getDescription();
        }
        else {
            return '';
        }
    }

    editParam(dataItem: any) {
        //;
        if (!this.currentOperation.Code) {
            alert("");
            return;
        }
        this.currentParam = Object.assign(new ComponentParameter(), dataItem);
        this.selectedParamList = [];
        this.selectedParamList.push(this.currentParam);
        this.resetParamRowSelection(this.currentParam);
        if (this.currentParamList.length == 1) {
            this.selectAllParamState = 'checked';
        }
        else if (this.currentParamList.length > 1) {
            this.selectAllParamState = 'indeterminate';
        }
        const dialogRef = this.dialogService.open({
            title: '',
            content: EditParamModalComponent,
            actions: [
                { text: '' },
                { text: '', primary: true }
            ],
            width: 400,
            height: 460
        });
        //
        const intance = dialogRef.content.instance;
        intance.currentParam = this.currentParam;
        if (this.currentParam && this.currentParam.DataType) {
            intance.dataTypeDescription = this.currentParam.DataType.getDescription();
        }
        else {
            intance.dataTypeDescription = null;
        }
        const actionEmitter = dialogRef.dialog.instance.action;
        dialogRef.dialog.instance.action = new EventEmitter<any>();
        const sub = dialogRef.dialog.instance.action.subscribe(action => {
            if (action.text == "") {
                actionEmitter.emit(action);
            }
            else {
                actionEmitter.emit(action);
            }
        });
        //
        dialogRef.result.subscribe((dialogResult) => {
            sub.unsubscribe();
            if (!(dialogResult instanceof DialogCloseResult)) {
                let actionName = (dialogResult as DialogAction).text;
                if (actionName == "") {
                    //;
                    let tempIndex = this.currentParamList.findIndex(value => value.Id == Object.assign(new ComponentParameter(), dialogRef.content.instance.currentParam).Id);
                    if (tempIndex != (-1)) {
                        this.currentParam = Object.assign(new ComponentParameter(), dialogRef.content.instance.currentParam)
                        this.currentParamList.splice(tempIndex, 1, this.currentParam);
                    }
                }
            }
        });
    }

    getParamModelList() {
        this.paramModelList = [];
        typeList.forEach(element => {
            this.paramModelList.push(element);
        });
        if (this.cmpmetadata.Models && this.cmpmetadata.Models.length > 0) {
            this.cmpmetadata.Models.forEach(element => {
                let tempModel = {} as TypeObject;
                // tempModel.text=element.code;
                // tempModel.value=element.modelId;
                this.paramModelList.push(tempModel);
            });
        }
    }

    resetParamRowSelection(dataTtem: any) {
        this.selectedParamIds = [];
        this.selectedParamIds.push(dataTtem.Id);
    }

    onSelectedParamChange(e) {
        //;
        this.selectedParamList = [] as ComponentParameter[];
        let len = this.selectedParamIds.length;
        if (len > 0) {
            this.selectedParamIds.forEach(id => {
                if (this.currentOperation.Parameters && this.currentOperation.Parameters.length > 0) {
                    let tempIndex = this.currentOperation.Parameters.findIndex(value => value.Id == id);
                    if (tempIndex != (-1)) {
                        this.selectedParamList.push(this.currentOperation.Parameters[tempIndex]);
                    }
                }
            });
        }
        if (len === 0) {
            this.selectAllParamState = 'unchecked';
        } else if (len > 0 && len < this.currentOperation.Parameters.length) {
            this.selectAllParamState = 'indeterminate';
        } else {
            this.selectAllParamState = 'checked';
        }
    }

    onSelectedOpChange(e) {
        this.selectedOpList = [] as ComponentMethod[];
        let len = this.selectedOpIds.length;
        if (len > 0) {
            this.selectedOpIds.forEach(id => {
                if (this.cmpmetadata.Methods && this.cmpmetadata.Methods.length > 0) {
                    let tempIndex = this.cmpmetadata.Methods.findIndex(value => value.Id == id);
                    if (tempIndex != (-1)) {
                        this.selectedOpList.push(this.cmpmetadata.Methods[tempIndex]);
                    }
                }
            });
        }
        if (len === 0) {
            this.selectAllOpState = 'unchecked';
        } else if (len > 0 && len < this.cmpmetadata.Methods.length) {
            this.selectAllOpState = 'indeterminate';
        } else {
            this.selectAllOpState = 'checked';
        }
    }

    onSelectAllOpChange(checkedState: SelectAllCheckboxState) {
        if (checkedState === 'checked') {
            this.selectedOpIds = this.cmpmetadata.Methods.map((item) => item.Id);
            this.selectAllOpState = 'checked';
        } else {
            this.selectedOpIds = [];
            this.selectAllOpState = 'unchecked';
        }
    }

    onSelectAllParamChange(checkedState: SelectAllCheckboxState) {
        if (checkedState === 'checked') {
            this.selectedParamIds = this.currentOperation.Parameters.map((item) => item.Id);
            this.selectAllParamState = 'checked';
        } else {
            this.selectedParamIds = [];
            this.selectAllParamState = 'unchecked';
        }
    }
} 


