import {ComponentFactoryResolver, Injector, Type} from '@angular/core';
import { ActionComponentDesignerPackageView } from './views';
import { CommonComponentDesignerComponent } from './views/acmpdesigner/ccmp.component';
import { Package, GSP } from '@farris/ide-devkit';

export class ActionComponentDesignerPackage extends Package {

  private layoutPanel: any;

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    
    this.gsp.workspace.addOpener((uri: string) => this.openAction(uri));
  }

  openAction(uri) {
    //;
    if (uri.endsWith('.action')) {
      const pkgView = new ActionComponentDesignerPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as CommonComponentDesignerComponent;
      currentElement.metadataUri = uri;
      const fileName = uri.substring(uri.lastIndexOf('/') + 1);
      return {
        getTitle: () => fileName,
        getElement: () => pkgView.getElement()
      };
    }
  }
}



